<template>
  <div class="register-page">
    <h1>Submit your Secret Code (Check your email)</h1>
    <form @submit="submitLoginForm">
      <input v-model="code" type="text" placeholder="code"/>
      <br/>
      <button type="submit">
        Submit Code
      </button>
    </form>

  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      code: ""
    };
  },
  methods: {
    async submitLoginForm(e) {
      e.preventDefault();
      if (this.code == "") {
        alert("Please fill code field");
      }

      let res = await apiRequest("two-factor-code-submit", {
        twoFactorCode: this.code
      });
      this.$router.push("/dashboard/");

      console.log("res: ", res);
    }
  }
}
</script>

