<template>
  <div class="apply-loan-account">
    <h1>View Loan Account</h1>
    <div v-if="isDataReady">

      {{account.loanName}}
      <br/>
      Total Amount Due: ${{(Number(account.principalBalance) + Number(account.interestBalance)).toFixed(2)}}
      <br/>

      <h2> Make Repayment </h2>
      <div>
        <label>Repayment amount:</label>
        <input v-model="repaymentAmount" type="string"/>
        <button @click="submitRepayment">
          Submit
        </button>
      </div>
      
      <br/>

      <h2> History </h2>

      <div v-for="transaction in transactions" :key="transaction.id">
        {{moment(transaction.entryDate).format("DD/MM/YYYY HH:mm")}}
        {{transaction.type}}
        {{transaction.balance}}
      </div>
    </div>
  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      mambuUser: {},
      pwcUser: {},
      account: null,
      transactions: [],
      isDataReady: false,
      selectedProduct: null,
      loanAmount: 0,
      interestRate: 5,
      repaymentPeriodCount: 1,
      repaymentPeriodUnit: "MONTHS",
      repaymentInstallments: 5,
      arrearsTolerancePeriod: 10,
      repaymentAmount: null
    };
  },
  async created() {
    await Promise.all([
      this.fetchMyProfile(),
      this.fetchLoanAccount()
    ]);

    this.isDataReady = true;
  },
  methods: {
    async fetchMyProfile() {
      let res = await apiRequest("my-profile", {
      });

      console.log("my-profile: ", res);

      this.mambuUser = res.mambuUser;
      this.pwcUser = res.pwcUser;
    },
    async fetchLoanAccount() {
      let {account, transactions} = await apiRequest("get-loan-account", {
        key: this.$route.params.id
      });

      console.log("account: ", account);
      console.log("transactions: ", transactions);

      this.account = account;
      this.transactions = transactions;
    },
    selectProduct(product) {
      console.log("product: ", product);
      this.selectedProduct = product;
    },
    async submitRepayment(e) {
      let res = await apiRequest("submit-loan-repayment", {
        key: this.$route.params.id,
        amount: this.repaymentAmount,
      });
      this.repaymentAmount = null;
      document.location.reload();
      console.log("submitRepayment: ", res);
    },
  }
}
</script>

