  .apply-loan-account {
    .products-list {
      display: flex;
      justify-content: space-around;

      .product-item {
        background: #bbbbbb;
        padding: 10px 20px;
        margin-top: 50px;
        margin-bottom: 20px;
        cursor: pointer;

        &.selected {
          background: orange;
          transform: scale(1.25);
        }
      }
    }
  }
</style>
