<template>
  <div class="apply-loan-account">
    <h1>Apply for Loan Account</h1>
    <div v-if="isDataReady">
      ID: {{mambuUser.encodedKey}} <br/>
      Name: {{mambuUser.firstName + " " + mambuUser.lastName}} <br/>

      <div class="products-list">
        <div :class="{'product-item': true, 'selected': product == selectedProduct}" v-for="(product, index) in products" :key="index"
        @click="selectProduct(product)">
          {{product.productName}}
        </div>
      </div>


      <div>
        <label>Loan Name</label>
        <input v-model="loanName" type="text"/>
      </div>
      <br/>

      <div>
        <label>Loan Amount</label>
        <input v-model.number="loanAmount" type="number"/> $ SGD
      </div>
      <br/>

      <div>
        <label>Interest Rate</label>
        <input v-model.number="interestRate" type="number"/> % per Year
      </div>
      <br/>

      <div>
        <label>Repaid Every</label>
        <input v-model.number="repaymentPeriodCount" type="number"/>
        <select v-model="repaymentPeriodUnit">
          <option value="DAYS">Days</option>
          <option value="WEEKS">Weeks</option>
          <option value="MONTHS">Months</option>
          <option value="YEARS">Years</option>
        </select>
        <label>Over</label>
        <input v-model="repaymentInstallments" type="number"/> Installments        
      </div>
      <br/>

      <div>
        <label>Arrears Tolerance Period</label>
        <input v-model.number="arrearsTolerancePeriod" type="number"/> Days
      </div>
      <br/>


      <button @click="applyForLoanAccount">
        Apply For Loan Account
      </button>



    </div>
  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      mambuUser: {},
      pwcUser: {},
      products: [],
      isDataReady: false,
      selectedProduct: null,
      loanAmount: 0,
      interestRate: 5,
      repaymentPeriodCount: 1,
      repaymentPeriodUnit: "MONTHS",
      repaymentInstallments: 5,
      arrearsTolerancePeriod: 10,
      loanName: ""
    };
  },
  async created() {
    await Promise.all([
      this.fetchMyProfile(),
      this.fetchLoanAccounts()
    ]);

    this.isDataReady = true;
  },
  methods: {
    async fetchMyProfile() {
      let res = await apiRequest("my-profile", {
      });

      console.log("my-profile: ", res);

      this.mambuUser = res.mambuUser;
      this.pwcUser = res.pwcUser;
    },
    async fetchLoanAccounts() {
      let res = await apiRequest("list-loan-products");

      console.log("loanProducts: ", res);

      this.products = res.loanproducts.filter(lp => lp.productName.includes("[Standard]"));

      if (this.products.length == 1) {
        this.selectedProduct = this.products[0];
      }
    },
    selectProduct(product) {
      console.log("product: ", product);
      this.selectedProduct = product;
    },
    async applyForLoanAccount(e) {
      let res = await apiRequest("create-loan-account", {
        productKey: this.selectedProduct.encodedKey,
        loanAmount: this.loanAmount,
        interestRate: this.interestRate,
        repaymentPeriodCount: this.repaymentPeriodCount,
        repaymentPeriodUnit: this.repaymentPeriodUnit,
        repaymentInstallments: this.repaymentInstallments,
        arrearsTolerancePeriod: this.arrearsTolerancePeriod,
        loanName: this.loanName
      });

      this.$router.push("/view-loan-account/"+res.loanData.loanAccount.encodedKey);

      console.log("applyForLoanAccount: ", res);
    }
  }
}
</script>

<style lang="scss">
  .apply-loan-account {
    .products-list {
      display: flex;
      justify-content: space-around;

      .product-item {
        background: #bbbbbb;
        padding: 10px 20px;
        margin-top: 50px;
        margin-bottom: 20px;
        cursor: pointer;

        &.selected {
          background: orange;
          transform: scale(1.25);
        }
      }
    }
  }
</style>
