import Vue from 'vue'
import Router from 'vue-router'

import MainLayout from './layouts/MainLayout';
import ClientList from './pages/ClientList';

import CredentialLayout from './layouts/CredentialLayout';
import Register from './pages/credentials/Register';
import Verify from './pages/credentials/Verify';
import Login from './pages/credentials/Login';
import TwoFactor from './pages/credentials/TwoFactor';

import Dashboard from './pages/main/Dashboard';
import ApplyForLoanAccount from './pages/main/ApplyForLoanAccount';
import ApplyForSavingsAccount from './pages/main/ApplyForSavingsAccount';
import ViewLoanAccount from './pages/main/ViewLoanAccount';
import ViewSavingsAccount from './pages/main/ViewSavingsAccount';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {path: "/", component: MainLayout, props: {page: ClientList}},
    {path: "/register", component: CredentialLayout, props: {page: Register}},
    {path: "/verify/:key", component: CredentialLayout, props: {page: Verify}},
    {path: "/login", component: CredentialLayout, props: {page: Login}},
    {path: "/two-factor", component: CredentialLayout, props: {page: TwoFactor}},

    {path: "/dashboard", component: MainLayout, props: {page: Dashboard}},
    {path: "/apply-loan-account", component: MainLayout, props: {page: ApplyForLoanAccount}},
    {path: "/apply-savings-account", component: MainLayout, props: {page: ApplyForSavingsAccount}},
    {path: "/view-loan-account/:id", component: MainLayout, props: {page: ViewLoanAccount}},
    {path: "/view-savings-account/:id", component: MainLayout, props: {page: ViewSavingsAccount}}
  ]
})
