<template>
  <div class="apply-loan-account">
    <h1>View Savings Account</h1>
    <div v-if="isDataReady">

      {{account.name}}
      <br/>
      Total Balance: ${{(Number(account.balance)).toFixed(2)}}
      <br/>

      <h2> Make Deposit </h2>
      <div>
        <label>Deposit amount:</label>
        <input v-model="depositAmount" type="string"/>
        <button @click="submitDeposit">
          Submit
        </button>
      </div>
      
      <br/>

      <h2> History </h2>

      <div v-for="transaction in transactions" :key="transaction.id">
        {{moment(transaction.entryDate).format("DD/MM/YYYY HH:mm")}}
        {{transaction.type}}
        {{transaction.balance}}
      </div>
    </div>
  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      mambuUser: {},
      pwcUser: {},
      account: null,
      transactions: [],
      isDataReady: false,
      depositAmount: null
    };
  },
  async created() {
    await Promise.all([
      this.fetchMyProfile(),
      this.fetchAccount()
    ]);

    this.isDataReady = true;
  },
  methods: {
    async fetchMyProfile() {
      let res = await apiRequest("my-profile", {
      });

      console.log("my-profile: ", res);

      this.mambuUser = res.mambuUser;
      this.pwcUser = res.pwcUser;
    },
    async fetchAccount() {
      let {account, transactions} = await apiRequest("get-savings-account", {
        key: this.$route.params.id
      });

      console.log("account: ", account);
      console.log("transactions: ", transactions);

      this.account = account;
      this.transactions = transactions;
    },
    selectProduct(product) {
      console.log("product: ", product);
      this.selectedProduct = product;
    },
    async submitDeposit(e) {
      let res = await apiRequest("submit-savings-deposit", {
        key: this.$route.params.id,
        amount: this.depositAmount,
      });
      this.depositAmount = null;
      document.location.reload();
      console.log("submitRepayment: ", res);
    },
  }
}
</script>

