async function apiRequest(path, data) {
  let res = await axios.post(`http://${location.host.split(":")[0]}:4000/` + path, data, {
    headers: {
      "Authorization": store.get("authToken")
    }
  });

  return res.data;
}


export {
  apiRequest
}