<template>
  <div class="apply-loan-account">
    <br/>


    <h1>Apply for Savings Account</h1>
    <div v-if="isDataReady">
      ID: {{mambuUser.encodedKey}} <br/>
      Name: {{mambuUser.firstName + " " + mambuUser.lastName}} <br/>

      <div class="products-list">
        <div :class="{'product-item': true, 'selected': product == selectedProduct}" v-for="(product, index) in products" :key="index"
        @click="selectProduct(product)">
          {{product.name}}
        </div>
      </div>

      <div>
        <label>Account Name</label>
        <input v-model="accountName" type="string"/>
      </div>
      <br/>

      <div>
        <label>Initial Deposit Amount</label>
        <input v-model.number="initialDepositAmount" type="number"/> $ SGD
      </div>
      <br/>


      <button @click="applyForLoanAccount">
        Apply For Savings Account
      </button>



    </div>
  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      mambuUser: {},
      pwcUser: {},
      products: [],
      isDataReady: false,
      selectedProduct: null,
      initialDepositAmount: 0,
      accountName: "Savings Account",
    };
  },
  async created() {
    await Promise.all([
      this.fetchMyProfile(),
      this.fetchLoanAccounts()
    ]);

    this.isDataReady = true;
  },
  methods: {
    async fetchMyProfile() {
      let res = await apiRequest("my-profile", {
      });

      console.log("my-profile: ", res);

      this.mambuUser = res.mambuUser;
      this.pwcUser = res.pwcUser;
    },
    async fetchLoanAccounts() {
      let res = await apiRequest("list-savings-products");

      console.log("savingsproduct: ", res);

      this.products = res.savingsproduct.filter(lp => lp.name.includes("[Standard]"));

      if (this.products.length == 1) {
        this.selectedProduct = this.products[0];
      }
    },
    selectProduct(product) {
      console.log("product: ", product);
      this.selectedProduct = product;
    },
    async applyForLoanAccount(e) {
      let res = await apiRequest("create-savings-account", {
        productKey: this.selectedProduct.encodedKey,
        initialDepositAmount: this.initialDepositAmount,
        accountName: this.accountName
      });
      console.log("applyForLoanAccount: ", res);
      this.$router.push("/view-savings-account/"+res.savingsData.savingsAccount.encodedKey);
    }
  }
}
</script>

<style lang="scss">
  .apply-loan-account {
    .products-list {
      display: flex;
      justify-content: space-around;

      .product-item {
        background: #bbbbbb;
        padding: 10px 20px;
        margin-top: 50px;
        margin-bottom: 20px;
        cursor: pointer;

        &.selected {
          background: orange;
          transform: scale(1.25);
        }
      }
    }
  }
</style>
