<template>
  <div class="dashboard">
    <h1>Dashboard</h1>
    <div v-if="isDataReady">
      ID: {{mambuUser.encodedKey}} <br/>
      Name: {{mambuUser.firstName + " " + mambuUser.lastName}} <br/>

      <h2>Loan Accounts</h2>
      <router-link class="loan-account"
      :to='`/view-loan-account/${loan.encodedKey}`' v-for="loan in loanAccounts" :key="loan.id">
        {{loan.loanName}}
      </router-link>

      <router-link class="button"
      :to='`/apply-loan-account`'>
        Apply For New Loan Account
      </router-link>


      <h2>Savings Accounts</h2>

      <router-link class="loan-account"
      :to='`/view-savings-account/${saving.encodedKey}`' v-for="saving in savingsAccounts" :key="saving.id">
        {{saving.name}}
      </router-link>
      <router-link class="button"
      :to='`/apply-savings-account`'>
        Apply For New Savings Account
      </router-link>

    </div>
  </div>
</template>

<script>
import { apiRequest } from '~/src/lib/api.js';
import store from "store";

export default {
  data() {
    return {
      mambuUser: {},
      pwcUser: {},
      isDataReady: false,
      savingsAccounts: [],
      loanAccounts: []
    };
  },
  async created() {
    await Promise.all([
      this.fetchMyProfile(),
      this.fetchMyAccounts()
    ]);
    this.isDataReady = true;
  },
  methods: {
    async fetchMyProfile() {
      let res = await apiRequest("my-profile", {
      });

      this.mambuUser = res.mambuUser;
      this.pwcUser = res.pwcUser;
    },
    async fetchMyAccounts() {
      let [savingsAccounts, loanAccounts] = await Promise.all([
        apiRequest("list-savings-accounts"),
        apiRequest("list-loan-accounts")
      ]);

      this.savingsAccounts = savingsAccounts;
      this.loanAccounts = loanAccounts;

      console.log("savingsAccounts: ", savingsAccounts);
      console.log("loanAccounts: ", loanAccounts);
    }
  }
}
</script>

<style lang="scss">
  .dashboard {
    .loan-account {
      display: block;
    }

    .button {
      display: inline-block;
      background: #0000c7;
      font-weight: bold;
      color: #eaeaea;
      padding: 10px 20px;
      margin-top: 20px;
    }
  }
</style>
